<template>
<!--  <div class="controll-area">
    <v-sheet class="sheets"></v-sheet>
  </div> -->
  <!-- fluid 用于撑开宽高 -->
  <div class="area-bg">
    <div class="outter-vedio-mod" ref='xsVedioWrapper' v-show="isMx">
      <!-- <ControllerSheet modName="Vedio">
        <VedioMod/>
      </ControllerSheet> -->
    </div>
    <div class="area-wrapper">
      <v-container class="controll-area">
        <v-row>
          <v-col md="6" sm="7" xs="12" order="6">
            <v-row class="innerRow">
              <v-col cols="12" ref='smVedioWrapper'>
                <ControllerSheet modName="Video" ref='VedioModDom'>
                  <VedioMod/>
                </ControllerSheet>
                <ControllerSheet modName="Video" v-show="isMx">
                  <div class="fake-vedio-wrapper"></div>
                </ControllerSheet>
              </v-col>
              <v-col cols="12" class="d-none d-sm-block">
                <ControllerSheet modName="Instruction">
                  <InstructionMod/>
                </ControllerSheet>
              </v-col>
            </v-row>
          </v-col>
          <v-col md="4" sm="5" xs="12" order="9">
            <v-row class="innerRow">
              <v-col cols="12">
                <ControllerSheet modName="Bewegungen">
                  <MoveControlMod/>
                </ControllerSheet>
              </v-col>
              <v-col cols="12">
                <ControllerSheet modName="Sensoren bewegen">
                  <ArmControlMod/>
                </ControllerSheet>
              </v-col>
              <v-col cols="12">
                <ControllerSheet modName="Linie mit Kamera verfolgen">
                  <CVFLMod/>
                </ControllerSheet>
              </v-col>
              <v-col cols="12">
                <ControllerSheet modName="Radar Scan Control">
                  <RadarScanMod/>
                </ControllerSheet>
              </v-col>
              <v-col cols="12">
                <ControllerSheet modName="PWM INIT SET">
                  <ServoInitMod/>
                </ControllerSheet>
              </v-col>
            </v-row>
          </v-col>
          <v-col md="2" sm="12" xs="12" order="12">
            <v-row class="innerRow">
              <v-col md="12" sm="3" xs="12" class="d-none d-sm-block">
                <ControllerSheet modName="Hardware">
                  <StatusMod/>
                </ControllerSheet>
              </v-col >
              <v-col md="12" sm="4" xs="12">
                <ControllerSheet modName="Actions">
                  <ActionsMod/>
                </ControllerSheet>
              </v-col>
              <v-col md="12" sm="5" xs="12">
                <v-row class="innerRow">
                  <v-col md="12" sm="12" xs="6">
                    <ControllerSheet modName="Farbe suchen">
                      <FindColorMod/>
                    </ControllerSheet>
                  </v-col>
                  <v-col md="12" sm="12" xs="6">
                    <ControllerSheet modName="Ports Control">
                      <PortsControlMod/>
                    </ControllerSheet>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import ControllerSheet from '@/components/controll/ControllerSheet.vue'
import VedioMod from '@/components/controll/VedioMod.vue'
import StatusMod from '@/components/controll/StatusMod.vue'
import ArmControlMod from '@/components/controll/ArmControlMod.vue'
import ActionsMod from '@/components/controll/ActionsMod.vue'
import MoveControlMod from '@/components/controll/MoveControlMod.vue'
import CVFLMod from '@/components/controll/CVFLMod.vue'
import FindColorMod from '@/components/controll/FindColorMod.vue'
import RadarScanMod from '@/components/controll/RadarScanMod.vue'
import InstructionMod from '@/components/controll/InstructionMod.vue'
import PortsControlMod from '@/components/controll/PortsControlMod.vue'
import ServoInitMod from '@/components/controll/ServoInitMod.vue'

export default ({
  name: 'ControlArea',
  components: {
    ControllerSheet,
    VedioMod,
    StatusMod,
    ArmControlMod,
    ActionsMod,
    MoveControlMod,
    CVFLMod,
    FindColorMod,
    RadarScanMod,
    InstructionMod,
    PortsControlMod,
    ServoInitMod
  },
  data: () => ({
    isMx: false // 判断是否让视频模块浮动
  }),
  watch: {
    isMx () {
      const vedioDom = this.$refs.VedioModDom.$el.parentNode.removeChild(this.$refs.VedioModDom.$el)
      if (this.isMx) {
        this.$refs.xsVedioWrapper.appendChild(vedioDom)
      } else {
        this.$refs.smVedioWrapper.appendChild(vedioDom)
      }
    }
  },
  methods: {
    checkIsMx () {
      this.isMx = (window.innerWidth < this.$vuetify.breakpoint.thresholds.xs && window.innerHeight/window.innerWidth > 1.5) // 除了屏幕宽度还要关注长宽比
    }
  },
  mounted () {
    this.checkIsMx()
    window.addEventListener('resize', this.checkIsMx)
  }
})
</script>

<style lang="stylus" scoped>
  @import '~@css/mixins.styl'
  @import '~@css/varibles.styl'

  .outter-vedio-mod
    position fixed !important
    overflow auto
    z-index 10
    padding 1rem
    padding-top 2rem
    background-color $default_bg_color
    box-shadow $lightShadow
    top 3rem
    left 0rem
    right 0rem

  .fake-vedio-wrapper
    position relative
    overflow hidden
    border-radius .4rem
    max-width 640px
    height 0
    width 100%
    padding-bottom 75%
    margin-top .5rem

  .area-bg
    display flex // 子元素垂直填充
    position absolute
    min-height 100%
    width 100%
  .area-wrapper
    margin 0 auto
    min-height 100%
    min-width 320px
    width 85rem
    max-width 85rem
    // background-color #FFFF99
    background-color $default_bg_color
  .innerRow
    margin -12px
  .controll-area
    min-height 100%
    max-width 80rem
    // min-width 320px
    .sheets
      background-color #b0bec5
</style>
